@import url('https://fonts.googleapis.com/css2?family=PT+Sans&family=Quicksand&family=Ubuntu&display=swap');

/* reset */
* {
  margin: 0px;
  padding: 0px;
}

/* Getting rid of spin in number input */
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0; 
}



/* NavBar */
nav {
  /* box-shadow: 0px 0px 8px black; */
  display: flex;
  align-items: center;
  background-color:aliceblue;
  margin-bottom: 20px;
}

#leftLinks {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  align-items: center;
}

#leftLinks a {
  text-decoration: none;
  color: black;
  font-size: 18px;
}

#leftLinks li {
  margin: 20px;
}

#leftLinks .heading {
  margin-top: 15px;
}

#leftLinks .heading a {
  font-size: 27px;
}


.rightPane {
  display: flex;
  flex-direction: row;
  position: absolute;
  right: 0px;
}

.rightPane .content {
  font-size: 19px;
  margin: auto 30px;
}

#acctAddress {
  border: solid 1.6px black;
  padding: 4px 8px;
  border-radius: 90px;
}


/* Slider */
.slideSection {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.slider {
  width: 53px;
  height: 27px;
  background-color: #414141;
  border-radius: 30px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.roller {
  width: 23px;
  height: 23px;
  border-radius: calc(100%);
  background-color: #e5e5e1;
  transform: translateX(2px);
  transition: all 0.15s ease-in 0s;
}



/* Lottery Transaction Section */
#lottery-section {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

#lottery-section .content {
  margin: 30px;
  /* box-shadow: 0px 0px 8px black; */
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 9px;
  width: 600px;
}

#lottery-section .content .contents {
  margin: 10px;
}

#lottery-section .content img {
  width: 500px;
  border-radius: 10px;
}

.highlight {
  background-color: #e5e1e1;
  border-radius: 8px;
  width: 100%;
  padding: 8px 1px;
}

#lottery-section .content h2 {
  text-align: center;
  font-size: 26px;
  font-family: 'Ubuntu';
}


#lottery-section .content p {
  font-family: 'Quicksand';
  font-weight: bold;
  font-size: 20px;
}

.buttonCSS {
  cursor: pointer;
  padding: 10px 20px;
  border-radius: 9px;
  font-size: 20px;
  transition: all 0.2s ease-in 0s;
  font-family: 'Ubuntu';
  border: none;
  box-shadow: 0px 0px 7px black;
  background-color: #e5e1e1;
}

.buttonCSS:active {
  transform: scale(0.96);
  box-shadow: none;
}

.response {
  padding: 4px;
}



/* Winner */
#winner {
  width: 470px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  padding: 40px 80px;
  border-radius: 9px;
  margin-bottom: 15px;
}

#winner h2 {
  font-family: 'Ubuntu';
  margin: 3px;
}

#winner h3 {
  font-family: 'Quicksand';
  font-size: 21px;
}


/* Alert */
#alert {
  z-index: 1;
  position: fixed;
  right: 0px;
  background-color: blanchedalmond;
  padding: 30px 20px;
  border-radius: 7px;
  display: flex;
  align-items: center;
  font-size: 17px;
}

#alert .crossSign {
  font-size: 34px;
  cursor: pointer;
  position: relative;
  bottom: 3px;
}


/* Donations Section */
#donationComponent {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Ubuntu';
}

.don-content {
  padding: 30px;
  font-family: 'Quicksand';
  width: 70%;
  border-radius: 9px;
}

.don-content div {
  margin: 5px;
}

.priceInput {
  width: 30%;
  margin-bottom: 12px;
}

.label {
  display: inline-block;
  font-size: 18px;
}

#donationAmt {
  padding: 10px;
  border: 0px;
  width: 90%;
  font-size: 17px;
}

#donationAmt {
  border: 0px;
  outline: none;
}